import React from "react";
import ReactDOM from "react-dom";
import { App } from "./App";
import { I18nextProvider } from "react-i18next";
import i18next from "i18next";

import common_de from "./languages/common_de.json";
import common_en from "./languages/common_en.json";

i18next.init({
  interpolation: { escapeValue: false },
  lng: "de",
  resources: {
    en: {
      common: common_en,
    },
    de: {
      common: common_de,
    },
  },
});

ReactDOM.render(
  <React.StrictMode>
    <I18nextProvider i18n={i18next}>
      <App />
    </I18nextProvider>
  </React.StrictMode>,
  document.getElementById("root")
);
